* {
  box-sizing: content-box;
  margin: 0px;
}

:root {
  /* define variables */
  --accent-light: #CBC6ED; /* #c51d2e or #ea9554 */
  --accent: #32568A;
  --accent-dark: #612fcc;
  --white: #FFFEFE;
  --white-dark: #F8F8F8;
  --gray: #959595;
  --black: #221D30;
  --title: "Inter";
  --title-fallback: serif;
  --title-resume: "Helvetica";
  --body: "Source Sans Pro";
  --body-fallback: serif;
}

body {
  background-color: #FFFEFE;
  background-color: var(--white);
  margin: auto;
  color: #221D30;
  color: var(--black);
}

/* add side margins with this div */
.body, .body-small {
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1100px;
  margin: auto;
}

.body-small {
  max-width: 700px;
  min-width: 0;
}

#page-container {
  min-height: 100vh;
  position: relative;
}

#content-wrap {
  padding-bottom: 150px;
}

/* customize appearance of text */
h1 {
  font-family: "Inter", serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 32px;
  font-weight: bold;
}

h2 {
  font-family: "Source Sans Pro", serif;
  font-family: var(--body), var(--body-fallback);
  font-size: 24px;
  font-weight: 500;
}

h3 {
  font-family: "Inter", serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 32px;
  /* color: var(--accent); */
  font-weight: bold;
}

h4 {
  font-family: "Source Sans Pro", serif;
  font-family: var(--body), var(--body-fallback);
  font-size: 16px;
  padding-left: 0px;
  color: #959595;
  color: var(--gray);
  font-weight: 400;
}

h5 {
  font-family: "Source Sans Pro", serif;
  font-family: var(--body), var(--body-fallback);
  font-size: 16px;
  font-weight: 400;
}

h6 {
  font-family: "Inter", serif;
  font-family: var(--title), var(--title-fallback);
  font-size: 42px;
  font-weight: bold;
}

p {
  font-family: "Source Sans Pro", serif;
  font-family: var(--body), var(--body-fallback);
  font-size: 16px;
  font-weight: 200;
}

a {
  color: #221D30;
  color: var(--black);
}

a:hover {
  color: #612fcc;
  color: var(--accent-dark);
}

.tag, button {
  font-family: "Source Sans Pro", serif;
  font-family: var(--body), var(--body-fallback);
  font-size: 12px;
  color: #221D30;
  color: var(--black);
  background-color: #F8F8F8;
  background-color: var(--white-dark);
  padding: 10px;
  text-decoration: none;
}

.tag {
  border-radius: 5px;
  max-width: -webkit-max-content;
  max-width: -moz-max-content;
  max-width: max-content;
  display: inline-block;
}

button {
  border-radius: 20px;
  border: 1px solid #221D30;
  border: 1px solid var(--black);
}

button:hover {
  background-color: #CBC6ED;
  background-color: var(--accent-light);
}

.small-btn {
  position: absolute;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  z-index: 9999;
  font-size: 18px;
}

.divider {
  padding: 10px;
}

.divider-big {
  padding: 40px;
}

hr {
  margin: 50px 0;
  border: 1px solid #CBC6ED;
  border: 1px solid var(--accent-light);
  top: 20px;
}


.unbold {
  font-weight: 400;
  display: inline-block;
}


/* two column structure */
.row, .row-2{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
}

/* align items at center, used in project card view */
.row {
  align-items: center;
  grid-gap: 10px 50px;
  gap: 10px 50px;
}

/* align items at top, used in project detail view */
.row-2 {
  align-items: flex-start;
  grid-gap: 20px 50px;
  gap: 20px 50px;
}

.col {
  flex: 1 1;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
  min-width: 360px;
}

.column > img {
  width: 100%; 
  min-width: 400px;
  height: auto; 
  border-radius: 5px;
}

.column > * {
  margin: 10px 0;
}

.col-big {
  flex: 2 1;
  min-width: 400px;
}

.col-small {
  min-width: 300px;
}

/* image above text when wrapped */
.reverse {
  flex-wrap: wrap-reverse;
}



@media screen and (max-width: 688px) {

  .body {
    margin: 0px 20px;
  }

  h1 {
    font-size: 24px;
  }
  
  h2 {
    font-size: 18px;
  }

  h3 {
    font-size: 16px;
  }

  h4, h5 {
    font-size: 14px;
  }

  h6 {
    font-size: 36px;
  }

  p, .tag {
    font-size: 12px;
  }

  .column, .column > img {
    min-width: 100%;
    max-width: 100%;
  }
}
#dev-body {
    background-color: var(--black);
    color: var(--white);
    margin: -20px 0;
    padding: 40px 0;
    z-index: -1;
}

#dev-body .tag {
    background-color: var(--accent-dark);
    color: var(--white);
}
.card-container {
    padding: 25px;
    position: relative;
}

.card-container-2 {
    padding: 15px;
}

.card-container:hover {
    color: var(--black);
    background-color: var(--accent-light);
    border-radius: 0 10px 10px 0;
    box-shadow: -500px 0 var(--accent-light);
    transition: box-shadow 750ms, background-color 750ms;
}

.card-link {
    text-decoration: none;
    color: var(--black);
}

/* surprise gif */
.card-gif {
    display: none;
    position: absolute;
    right: 10px;
    bottom: 0;
    max-width: 200px;
    height: auto;
}

.card-container:hover .card-gif {
   display: block;
}

.featured-tags {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    flex-wrap: wrap;
}

.card-img {
    background-color: var(--white-dark);
}


@media screen and (min-width: 870px) {
    /* align text to the right when image is on the right */
    .align-right {
        margin-left: auto; 
        margin-right: 0;
        text-align: right;
    }
    .align-right > .featured-tags {
        justify-content: flex-end;
    }
}

@media screen and (max-width: 688px) {
    .card-container {
        padding: 10px 0px;
        position: relative;
    }
}
.purple-tag {
    background: var(--accent-lighter);
    padding: 5px 10px;
    border-radius: 5px;
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
    margin: 0px 15px 10px 0;
    display: inline-block;
    font-family: var(--body), var(--body-fallback);
    font-size: 10px;
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);

    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}

.popup-inner {
    position: relative;
    padding: 30px;
    height: 80%;
    background-color: var(--white-dark);
    border-radius: 20px;
}

.popup-inner video {
    max-height: 100%;
}

.popup-button {
    top: 16px;
    right: 16px;
}
